body {
  height: 100%;
}

body {
  display: grid;
  grid-template-areas:
    "header main main"
    "sidebar main main"
    "sidebar main main";
  grid-template-rows: 1fr 12fr;
  grid-template-columns: 1fr 3fr 3fr;
  background-color: var(--bs-secondary-bg);
}

header {
  grid-area: header;
}

#sidebar {
  grid-area: sidebar;
}

#main-content {
  grid-area: main;
  background-color: var(--bs-tertiary-bg);
}

.accordion-body label:hover,
.list-group label:hover,
.list-group input:hover {
  cursor: pointer;
}

.card {
  width: 25rem;
  padding: 0;
}

.card:hover {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

h5 {
  margin: 0;
}

.card-header,
.card-li-container {
  display: flex;
}

.list-dropdown,
.task-dropdown {
  margin-left: auto;
  height: 0;
}

.list-dropdown-btn {
  border: none;
  background-color: transparent;
}

.task-dropdown {
  color: var(--bs-body-color);
  background-color: inherit;
  border: none;
  display: none;
}

li:hover .task-dropdown {
  display: inline;
}

.description {
  font-size: 0.9rem;
  margin-left: 1.6rem;
  opacity: 0.7;
  max-width: 310px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.date {
  font-size: 0.9rem;
  width: fit-content;
  margin-left: 1.5rem;
  margin-top: 3px;
  padding: 0px 10px;
  border: 1px solid var(--bs-border-color);
  border-radius: 1rem;
  background-color: var(--bs-tertiary-bg);
  opacity: 0.9;
}

/* Get rid of list accordion border radius */
.list-group-item:first-child {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.list-empty-state {
  font-size: 3rem;
  padding: 1.2rem;
  text-align: center;
}

.completed-tasks-header {
  padding: 8px 16px;
  margin: 0;
}

.completed-task-label {
  cursor: default !important;
  text-decoration: line-through;
  text-decoration-thickness: 2px;
  text-decoration-color: var(--bs-body-color);
}

.completed-task-delete-btn {
  margin-left: auto;
  border: none;
  background-color: inherit;
  display: none;
  height: 0;
}

li:hover .completed-task-delete-btn {
  display: inline;
}
